










































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import MyButton from '@/components/MyButton.vue';
import tagHelper from '@/mixins/TagHelper'
import {mixins} from 'vue-class-component';

@Component({
  components: {Button: MyButton},
  mixins:[tagHelper],
})
export default class Labels extends mixins(tagHelper) {
  get tags(){
    return this.$store.state.tagList
  }
  beforeCreate(){
    this.$store.commit('fetchTags');
  }
  remove(tag: Tag){
    this.$confirm('此操作将永久删除该标签, 是否继续?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
      customClass:'confirm-box',
      center: true
    }).then(() => {
      this.$store.commit('removeTag',{id:tag.id,_this:this})
    }).catch(() => {
      this.$message({
        type: 'info',
        message: '已取消删除',
        duration:1000,
        center:true
      });
    });
  }
  updateTag(value: {id: string;name: string;type: string}){
    if(value){
      if(value.name){
        this.$store.commit('updateTag',{id:value.id,name:value.name,type:value.type,_this:this})
      }else{
        this.$message({
          message: "标签名不能为空",
          type: "warning",
          center:true
        });
      }

    }
  }
  typeChange(value: {id: string;name: string;type: string}){
    if(value){
      if(value.name){
        this.$store.commit('updateTag',{id:value.id,name:value.name,type:value.type,_this:this})
      }else{
        this.$message({
          message: "标签名不能为空",
          type: "warning",
          center:true
        });
      }
    }
  }
}
