







































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import FormItem from '@/components/Money/FormItem.vue';
import {Button} from 'element-ui';

type newLabelOption = {
  name: string; type: string; icon: string;
}
@Component({
  components: {FormItem,Button}
})
export default class NewLabel extends Vue {

  get switch(){
    return true
  }
  typeSwitch = this.switch;
  newIconList = [
    {name: '#公交车'},
    {name: '#厨房电器'},
    {name: '#地铁'},
    {name: '#奖励'},
    {name: '#家用电器'},
    {name: '#家用电器(1)'},
    {name: '#工资'},
    {name: '#工资单'},
    {name: '#工资卡认证'},
    {name: '#房子'},
    {name: '#房子(1)'},
    {name: '#汽车'},
    {name: '#汽车(1)'},
    {name: '#泳装'},
    {name: '#玩具'},
    {name: '#电脑电器'},
    {name: '#红包(1)'},
    {name: '#红包'},
    {name: '#股票(1)'},
    {name: '#股票'},
    {name: '#船'},
    {name: '#船舶'},
    {name: '#衣服'},
    {name: '#衣服(1)'},
    {name: '#衣服(2)'},
    {name: '#衣服2'},
    {name: '#钱包(1)'},
    {name: '#钱包(2)'},
    {name: '#钱包'},
    {name: '#飞机'},
    {name: '#飞机(1)'},
    {name: '#飞机(2)'},
    {name: '#食物'},
    {name: '#食物(1)'},
    {name: '#食物(2)'},
    {name: '#食物(3)'},
    {name: '#食物-53'},


  ];
  selectedIcon: string[] = [];

  switchWidth = 100;
  newLabelOption = {
    name: '',
    type: '-',
    icon: ''
  };
  created(){
    this.$store.commit('fetchTags')
  }
  toggle(item: string) {
    const index = this.selectedIcon.indexOf(item);
    if (index) {
      this.selectedIcon.splice(index, 1);
      this.selectedIcon.push(item);
      this.newLabelOption.icon = item;
    }
  }
  goBack(){
    this.$router.back();
  }
  ok(){
    if (!this.newLabelOption.name) {
      this.$message({
        message: '标签名不能为空',
        type: 'warning',
        center:true
      });
      return
    } else {
      const name = this.newLabelOption.name.slice(0, 4).toString();
      if(this.selectedIcon.length > 0 && this.newLabelOption.icon){
        const names = this.$store.state.tagList.map((item: Tag) => item.name);
        const index = names.indexOf(name);
        let types
        if (index >= 0){
          types = this.$store.state.tagList[index].type;
        }
        if (names.indexOf(name) >= 0 && this.newLabelOption.type === types) {
          //return window.alert("当前分类已有相同标的签名,请换个名字");
          this.$message({
            type: "error",
            message: '当前分类已有相同标的签名,请换个名字',
            center: true,
          });
          return
        }else{
          this.$store.commit("createTag", {
            name,
            type: this.newLabelOption.type,
            icon: this.newLabelOption.icon,
            _this:this}
            );
        }
        this.$router.back();
      }else{
        this.$message({
          message: "请选择一个标签",
          type: "warning",
          center:true
        });
        return
      }
    }
  }
}
